import pick from 'lodash/fp/pick';

const fields = [
  'newOrExisting',
  'platforms',
  'users',
  'description',
  'stack',
  'history',
  'devs',
  'team',
  'location',
  'startDate',
  'contact',
];

const filterFields = pick(fields);

const giveSummary = session => {
  const data = filterFields(session.privateConversationData);
  newOrExisting({ data, session });
};

export default [giveSummary];
